<template>
  <!-- 组件列表展示 -->
  <div class="content">
    <!-- 滚动条盒子 -->
    <div id="scrollContainer" class="layout">
      <p class="tit">自定义滚动条（scrollContainer）</p>
      <div class="layout-box">
        <scrollContainer
          ref="scrollbarRef"
          height="100%"
          class="scroll-container"
        >
          <ul v-for="item in 17" :key="item">
            <li>{{ item }} 信息列表</li>
          </ul>
        </scrollContainer>
        <a
          class="my_a"
          href="/#/details?article=custom_scrollContainer&date=2021-11-23%2022%3A59%3A00&tag=vue&tag=javaScript&tag=%E5%85%AC%E5%85%B1%E7%BB%84%E4%BB%B6"
          >查看代码
        </a>
      </div>
    </div>

    <!-- 确认提示框-->
    <div id="Confirm" class="layout">
      <p class="tit">确认提示框</p>
      <p @click="handMe()">确认提示框</p>
      <a
        class="my_a"
        href="/#/details?article=custom_scrollContainer&date=2021-11-23%2022%3A59%3A00&tag=vue&tag=javaScript&tag=%E5%85%AC%E5%85%B1%E7%BB%84%E4%BB%B6"
        >查看代码
      </a>
    </div>
  </div>
</template>
<script>
export default {
  mounted() {
    // *注：demo更新需要更新容器滚动条
    this.$nextTick(() => {
      this.$refs.scrollbarRef.updateScroll();
    });
  },
  methods: {
    // 确认提示框调用方式
    handMe() {
      this.$confirm({
        type: "提示标题",
        msg: "提示内容自定义，全局使用",
        btn: {
          ok: "确定",
          no: "取消",
        },
      })
        .then(() => {
          alert("确认");
        })
        .catch(() => {
          alert("取消");
        });
    },
  },
};
</script>

<style lang="less" scoped>
.layout {
  padding: 20px;
  width: 90%;
  margin: 0 auto;
  margin-top: 30px;
  box-sizing: border-box;
  overflow: hidden;
  .tit {
    margin: 10px 0;
    box-sizing: border-box;
    font-size: 20px;
    font-weight: 700px;
  }
}
// 滚动条盒子
.layout-box {
  border: 1px solid #ddd;
  margin: 0 auto;
  height: 200px;
}
.my_a {
  text-decoration: none;
  color: rgb(0, 60, 255);
  line-height: 40px;
  font-size: 18px;
  float: right;
}
</style>
