<template>
  <div class="suptopUibox" :class="{ suptopUi_navshow: suptopUi_navSwitch }">
    <!-- 左侧导航 -->
    <div class="suptopUi_nav">
      <a
        class="navitem"
        @click="navClick(nav.EN)"
        :class="{ active: navitem == nav.EN }"
        v-for="nav in navlist"
        :key="nav.EN"
        >{{ nav.CH }}</a
      >
    </div>

    <div class="suptopUi_components">
      <!-- 控制导航显示隐藏按键 -->
      <span
        class="suptopUi_navbtn iconfont icon-24px"
        @click="suptopUi_navSwitch = !suptopUi_navSwitch"
      ></span>
      <!-- 内容列表 自定义滚动条-->
      <scrollContainer
        ref="scrollbarRef"
        height="100%"
        class="scroll-container"
      >
        <!-- 组件展示列表 -->
        <assemblyList />
      </scrollContainer>
    </div>
  </div>
</template>
<script>
import assemblyList from "./assembly_List";
export default {
  components: {
    assemblyList,
  },
  data() {
    return {
      suptopUi_navSwitch: false, //导航控制显示隐藏
      navitem: "", //选中导航元素值
      navlist: [
        //导航数组Confirm
        { EN: "scrollContainer", CH: "自定义滚动条" },
        { EN: "Confirm", CH: "确认提示框" },
      ],
    };
  },
  mounted() {
    // *注：demo更新需要更新容器滚动条
    this.$nextTick(() => {
      this.$refs.scrollbarRef.updateScroll();
    });
  },
  methods: {
    // 问题 a标签锚点 让#不在url上显示，
    // 解决 在代码中跳转
    navClick(idName) {
      this.suptopUi_navSwitch = false; //小窗口点击导航就隐藏导航
      this.navitem = idName; //切换选中a标识
      document.querySelector("#" + idName).scrollIntoView(true); //'#'+idName   #Drop  锚点跳转   元素id = 'Drop'
    },
  },
};
</script>
<style lang="less" scoped>
/* 整体页面 */
.suptopUibox {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  .suptopUi_nav {
    width: 250px;
    height: 100%;
    position: absolute;
    padding-top: 10px;
    border-radius: 0px 5px 5px 0px;
    z-index: 888;
    top: 0;
    left: 0;
    box-sizing: border-box;
    overflow: auto;
    transition: 0.3s;
    .navitem {
      cursor: pointer;
      display: block;
      padding: 0 20px;
      line-height: 40px;
      height: 40px;
      text-decoration: none;
      font-size: 16px;
      transition: 0.3s;
      color: rgb(0, 0, 0);
      box-sizing: border-box;
      transition: 0.3s;
      overflow: hidden;
      &:hover,
      &.active {
        color: #409eff;
      }
    }
  }
  .suptopUi_components {
    width: 100%;
    height: 100%;
    padding-left: 250px;
    box-sizing: border-box;
    overflow: auto;
    transition: 0.3s;
    .suptopUi_navbtn {
      cursor: pointer;
      font-size: 20px;
      display: none;
      position: fixed;
      transition: 0.3s;
      z-index: 990;
      .content {
        height: 100%;
      }
    }
  }
}

.min .suptopUi_nav {
  width: 0;
}
.min .suptopUi_navshow .suptopUi_nav {
  width: 200px;
}
.min .suptopUi_navshow .suptopUi_navbtn {
  padding-left: 200px;
}

.min .suptopUi_components {
  padding-left: 0px;
}

.min .suptopUi_components .suptopUi_navbtn {
  width: 30px;
  display: block;
}
.min .suptopUi_components .suptopUi_navshow .suptopUi_navbtn {
  padding-left: 200px;
}
</style>
